<template>
	<view class="uni-common-mt">
		<form >
			<input class="hidden" v-model="formData.leaveId"  v-if="edit=='1'"/>
			<view class="uni-form-item uni-row">
				<view class="title">申请人</view>
				<input class="uni-input" focus placeholder="申请人" :value="user.userName" />
			</view>
			<view class="uni-form-item uni-row">
				<view class="title">申请公司</view>
				<input class="hidden" name="applyCompanyId" v-model="formData.applyCompanyId">
				<input class="hidden" name="deptId" v-model="formData.deptId">
				<picker  class="picker" mode="multiSelector" @columnchange="bindMultiPickerColumnChange" :value="multiIndex" :range="multiArray">
					<view class="uni-input">{{multiArray[0][multiIndex[0]]}}，{{multiArray[1][multiIndex[1]]}}，{{multiArray[2][multiIndex[2]]}}</view>
				</picker>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>请假起始时间</view>
				<input class="hidden" name="applyTimeFrom" focus v-model="formData.applyTimeFrom"/>
				<e-picker class="uni-input" mode="dateTime" :showValue="(formData.applyTimeFrom?formData.applyTimeFrom:dateStart)"  @change="changeStart">{{(formData.applyTimeFrom?formData.applyTimeFrom:dateStart)}}</e-picker>
				<!-- <input class="uni-input" name="goOutStart" focus placeholder="外出起始时间" /> -->
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>请假结束时间</view>
				<input class="hidden" name="applyTimeTo"  focus v-model="formData.applyTimeTo"/>
				<e-picker class="uni-input" mode="dateTime" :showValue="(formData.applyTimeTo?formData.applyTimeTo:dateEnd)"  @change="changeEnd">{{(formData.applyTimeTo?formData.applyTimeTo:dateEnd)}}</e-picker>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>请假时长</view>
				<input class="uni-input" name="leaveDays"  disabled="true"  focus placeholder="请假时长" v-model="formData.leaveDays"/>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>请假类别</view>
				<input class="hidden" name="leaveType" focus v-model="formData.leaveType"/>
				<picker class="picker" @change="bindPickerChange" :value="index" :range="leaveType" range-key="dictLabel">
					<view class="uni-input">{{(leaveType[index]?leaveType[index]:{}).dictLabel}}</view>
				</picker>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title">剩余年假</view>
				<input class="uni-input" name="yearHoliday"  focus placeholder="剩余年假" disabled="true" v-model="user.yearHoliday"/>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>请假事由</view>
				<input class="uni-input" name="leaveReason" v-model="formData.leaveReason"  focus placeholder="请假事由" />
			</view>
			<view class="uni-form-item uni-row">
				<view class="title">备注</view>
				<input class="uni-input" name="remark"  focus placeholder="备注" v-model="formData.remark"/>
			</view>
			<view class="button-sp-area">
				<button class="mini-btn" type="info" size="mini" :loading="loading1" @click="submit('0')" v-if="formData.status=='99'||formData.status==''">保存草稿</button>
				<button class="mini-btn" type="primary" @click="submit('1')" :loading="loading2" v-if="formData.status=='99'||formData.status==''"  size="mini">提交</button>
				<button class="mini-btn" type="primary" @click="submitHandler('2')" v-if="formData.status=='3'"  size="mini">提交</button>
				<button class="mini-btn cui-btn-2" type="info" :loading="loading3" @click="urge()"  v-if="formData.status!=''&&formData.status!='2'&&formData.status!='16'&&formData.status!='99'"  size="mini">催办</button>
				<button class="mini-btn cui-btn-1" type="info"  v-if="formData.status!='99'&&formData.status!=''"  size="mini" @click="send()">抄送</button>
			</view>
		</form>
		<uni-process :baseUrl="baseUrl" :procInstId="procInstId" :mid="mid" v-if="edit=='1'&&procInstId"></uni-process>
	</view>
</template>
<script>
	import service from '@/service.js';
	import graceChecker from '@/common/graceChecker.js'
	import ePicker from '@/components/e-picker/e-picker.vue';
	import uniProcess from '@/components/uni-processList/nui-processList.vue';
	export default {
		components: {
			ePicker,
			uniProcess
		},
		data() {
			return {
				mid:'',
				edit:'',
				user:{},
				formData:{
					leaveId:'',
					applyCompanyId:'',
					deptId:'',
					applyTimeFrom:service.getDate('dateTime'),
					applyTimeTo:service.getDate('dateTime'),
					leaveDays:0,
					leaveType:'0',
					yearHoliday:0,
					leaveReason:'',
					remark:'',
					status:''
				},
				baseUrl:'oa/oaLeave',
				addUrl:'oa/oaLeave/add',
				saveUrl:'oa/oaLeave/saveDraft',
				leaveType: [],
				index: 0,
				date: service.getDate('dateTime'),
				dateStart:service.getDate('dateTime'),
				dateEnd:service.getDate('dateTime'),
				timeLimit:'',
				company:[],
				multiArray: [
					['百川'],
					[],
					[]
				],
				multiArrayId: [
					['100'],
					[],
					[]
				],
				multiIndex: [0, 0, 0],
				loading1:false,
				loading2:false,
				loading3:false,
				procInstId:''
			}
		},
		onLoad(e) {
			this.edit=e.edit || ''
			let this_=this
			this.mid = e.id || ''
			//用户信息
			this.urlRequestPost('/phoneIndexData','',null,function(res){
				if(res.statusCode='200'){
					this_.user=res.data.user
					this_.$set(this_.formData,'applyCompanyId',res.data.user.companyId)
					this_.$set(this_.formData,'deptId',res.data.user.deptId)
					// 公司集合
					this_.urlRequestGet('system/dept/list?parentId=100','text/html;charset=UTF-8',function(res){
						if(res.statusCode='200'){
							this_.company=res.data;
							let companyList=[]
							let companyIdList=[]
							let initId=0
							for (let i in this_.company) {
								if(this_.company[i].deptId==this_.user.companyId){
									initId=i
								}
								companyList.push(this_.company[i].deptName)
								companyIdList.push(this_.company[i].deptId)
							}
							this_.multiArray[1]=companyList
							this_.multiArrayId[1]=companyIdList
							//默认展示的是当前用户的公司
							this_.showDeptDetail(initId)
						}
					})
				}
			})
			//字典信息
			this.dictValue('oa_leave_type').then(res => {
				// console.log(res)
				this_.leaveType=res.data
			})
			//编辑页面信息
			if(this.edit=='1'){
				this.urlRequestGet('oa/oaLeave/phoneEdit/'+this.mid,'text/html;charset=UTF-8',function(res){
					this.listAssign(this_.formData,res.data)
					this_.index=this_.returnIndex(this_.leaveType,this_.formData.leaveType)
					// console.log(res.data)
					this_.procInstId=res.data.procInstId
					//console.log(this_.procInstId)
				})
				
			}
		},
		onReady() {
			let this_=this
			
			
		},
		methods: {
			// 提交和保存
			submit(flag){
				let this_=this
				let url = ''
				//定义表单规则			
				let rule = [
				    {name:"applyTimeFrom", checkType : "notnull", checkRule:"",  errorMsg:"开始时间必填"},
				    {name:"applyTimeTo", checkType : "notnull", checkRule:"",  errorMsg:"结束时间必填"},
				    {name:"leaveDays", checkType : "notnull", checkRule:"",  errorMsg:"时长必填"},
					{name:"leaveType", checkType : "notnull", checkRule:"",  errorMsg:"类别必填"},
					{name:"leaveReason", checkType : "notnull", checkRule:"",  errorMsg:"理由必填"}
				];
				
				//进行表单检查
				let checkRes = graceChecker.check(this.formData, rule);
				if(checkRes){
					// console.log(this.formData)
					switch(flag){
						case '1'://1代表的是提交
						     url=this.addUrl
							 this.loading2=true
							 break
					    case '0'://0代表的是保存
						     url=this.saveUrl
							 this.loading1=true
							 break
					}
					this.urlRequestPost(url,this.formData,null,function(res){
						// console.log(res)
						this_.loading1=false
						this_.loading2=false
						uni.showToast({title:res.data.msg, icon:"none"});
						if(res.data.code=='0'){
							uni.reLaunch({
							    url: '../../start/start'
							});
						}
					})
				}else{
				    uni.showToast({ title: graceChecker.error, icon: "none" });
				}
			},
			bindPickerChange: function(e) {
				this.index = e.target.value
				// 请假类别赋值
				this.formData.leaveType=e.target.value
			},
			changeStart: function(e) {
				this.formData.applyTimeFrom=e
				this.showValue = e
				this.dateStart = e
				this.getTimeLimit(this.dateStart,this.dateEnd)
			},
			changeEnd: function(e) {
				this.formData.applyTimeTo=e
				this.showValue = e
				this.dateEnd = e
				this.getTimeLimit(this.dateStart,this.dateEnd)
			},
			getTimeLimit(start,end){
				let this_=this
				let dataTime={}
				dataTime.startTime=start
				dataTime.endTime=end
				this.urlRequestPost('oa/oaLeave/calculateHour',JSON.stringify(dataTime),'application/json;charset=UTF-8',function(res){
					//this_.formData.leaveDays=res.data.hours
					console.log(res)
					if(res.data.retCode=='200'){
						this_.$set(this_.formData,'leaveDays',res.data.hours)
					}else{
						uni.showToast({title:res.data.msg, icon:"none"});
					}
				})
			},
			returnIndex(n,v){
				var index=''
				for (let i in n) {
					if(n[i].dictValue==v){
						index=i
					}
				}
				return index
			},
			send(){
				uni.navigateTo({
				    url: "../../send/send?id="+this.mid+"&url="+this.baseUrl,
				});
			},
			urge(){
				var this_=this
				this.loading3=true
				this.urlRequestGet(this.baseUrl+'/urge/'+this.mid,null,function(res){
					//this_.formData.leaveDays=res.data.hours
					//console.log(res)
					uni.showToast({title:res.data.msg, icon:"none"});
					if(res.data.code==0){
						this_.loading3=false
						uni.reLaunch({
						    url: '../../start/start'
						});
					}
					
				})
			},
			bindMultiPickerColumnChange: function(e) {
				this.multiIndex[e.detail.column] = e.detail.value
				if(e.detail.column==1){
					this.showDeptDetail(e.detail.value)
					//this.multiIndex.splice(2, 1, 0)
				}
				if(e.detail.column==2){
					this.formData.deptId=this_.multiArrayId[2][e.detail.value]
				}
				this.$forceUpdate()
			},
			showDeptDetail(index){
				var this_=this
				this.urlRequestGet('system/dept/list?parentId='+this.company[index].deptId,'text/html;charset=UTF-8',function(res){
					if(res.data.code='0'){
						let list=[]
						let listId=[]
						// console.log(res.data)
						for (let i in res.data) {
							list.push(res.data[i].deptName)
							listId.push(res.data[i].deptId)
						}
						this_.multiArray[2]=list
						this_.multiArrayId[2]=listId
						this_.multiArray.splice(0,0)
						this_.multiArrayId.splice(0,0)
						// this_.formData.applyCompanyId=this_.multiArrayId[1][this_.multiIndex[1]]
						// this_.formData.deptId=this_.multiArrayId[2][0]
						//默认展示
						
						this_.multiIndex[1]=index
						//this_.multiIndex.splice(2, 1, 0)
						
					}
				})
			}
		}
	}
</script>

<style>
	@import url("../../../static/css/oa.css");
</style>
